@import url(https://fonts.googleapis.com/css2?family=Leckerli+One&family=Tajawal:wght@500&display=swap);
body {
  margin: 0;
  font-family: 'Tajawal', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, #root {
    width: 100%;
    font-family: 'Tajawal', sans-serif;
    font-size: 20px;
}

html {
    scroll-behavior: smooth;
}

.header-container {
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.sections-container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-content: center;
}

.container-fluid {
    padding: 0 !important;
}

#home {
    background-color: #094785;
}

.title-header {
    font-size:  3em !important;
    color: white;
}

.title-sections {
    font-size: 2.5em;
    margin-top: 10vh;
    text-align: center;
}

.col-1 {
    height: 20%;
}

.sections-container p {
    width: 67%;
    font-size: 2.3em;
    text-align: justify;
    margin: auto;
}
